            >
            <v-card-text class="left-text answer-tag">
              {{ item.num_variable }}
            </v-card-text>
            <v-card-title class="left-text info-tag" lg="12">
              Time for 1000 * 10000 runs(ms)
            </v-card-title>

            <div class="scroll" v-html="item.table_html"></div>

            <v-card-title class="left-text info-tag" lg="12">
              Interval size
            </v-card-title>
            <v-row>
              <v-col class="system-tag" lg="6"> Windows </v-col>
              <v-col class="system-tag" lg="6"> MacOS(x86_64) </v-col>
            </v-row>
            <v-row>
