                <v-img
                  :src="returnImage(item, 'Windows-10-10.0.19041-SP0')"
                ></v-img>
              </v-col>
              <v-col class="system-tag" lg="6">
                <v-img
                  :src="returnImage(item, 'Darwin-20.1.0-x86_64-i386-64bit')"
