<script>
export default {
  name: "Benchmark",
  components: {},
  data() {
    return {
      num_benchmark: 10,
      benchmark_infos: [],
      expanded: [],
    };
  },
  methods: {
    returnImage(item, system) {
      var base = item.graph_name_base;
      return require("../assets/graphs/raw/" +
        base +
        "_gap_raw_" +
        system +
        ".png");
    },
  },

  mounted() {
    this.benchmark_infos = require("../assets/expression_infos.json");
    this.benchmark_infos = this.benchmark_infos.sort(function (a, b) {
      var x = a.description;
      var y = b.description;
      return x < y ? -1 : x > y ? 1 : 0;
    });
    for (var i = 0; i < this.benchmark_infos.length; i++) {
      this.benchmark_infos[i]["id"] = i;
    }
    this.num_benchmark = this.benchmark_infos.length;
    this.expanded = Array(this.num_benchmark).fill(false);
  },
};
</script>

