<template>
  <div class="About">
    <h1 class="display-4 text-center" id="intro-header" style="color: #bcd6f0">
      Interval Benchmarks
    </h1>
    <div class="center-intro">
      <v-card width="100%" color="rgba(20, 32, 36, 0.2)" dark elevation="10">
        <v-card-title class="about-titles">Why?</v-card-title>
        <v-card-text class="about-paragraphs">
          Interval computation allows us to perform floating point operations
          with certifiable correctness, by accounting for rounding errors. Every
          floating point number is replaced by a pair of numbers, representing
          an interval that contains the result of the floating point
          computation, for any possible rounding. While this approach increases
          the cost and memory usage of computations, it is a staple for many
          algorithms in geometric computing, image processing, computer
          graphics, and scientific computing.
        </v-card-text>
        <v-card-text class="about-paragraphs">
          We use this benchmark to check the correctness, portability, interval
          size and speed of an interval library. The benchmark is designed so
          that users can deploy it to different platform/architectures, easily
          add new library and expressions for testing.
        </v-card-text>
      </v-card>
    </div>

    <div class="center-intro">
      <v-card width="100%" color="rgba(20, 32, 36, 0.2)" dark elevation="10">
        <v-card-title class="about-titles">How?</v-card-title>
        <v-card-text class="about-paragraphs">
          To compile the existing benchmarks, simply do:
          <br />
          <br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="bash">git clone https://github.com/geometryprocessing/intervals
cd intervals
mkdir build
cd build && cmake .. -DCMAKE_BUILD_TYPE=Release
make -j4</code></pre>
          <br />
          Then to run tests and generate figures/tables, run:
          <br />
          <br />
          <pre class="code-text" v-highlightjs><code class="bash">cd ..
./test_gap.sh
./test_speed.sh
./test_query.sh</code></pre>
          <br />Note that you will need
          <a href="https://www.wolfram.com/wolframscript/" target="_blank"
            >WolframScript</a
          >
          to check the correctness. You can also substitute it with the software
          you want. We will explain what a query like soon
        </v-card-text>
      </v-card>
    </div>
    <div class="center-intro">
      <v-card width="100%" color="rgba(20, 32, 36, 0.2)" dark elevation="10">
        <v-card-title class="about-titles"
          >Adding an interval library</v-card-title
        >
        <v-card-text class="about-paragraphs">
          Since this is a cmake project, the first thing you need to do is edit
          the cmake file to include the library. Then, adding an new library is
          done by editing
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/src/interval.hpp"
            target="_blank"
            >src/interval.hpp</a
          >. There are a couple of things you need to define in order for the
          library to work:
        </v-card-text>
        <v-card-title class="about-subtitle"
          >Initialization with single value</v-card-title
        >
        <v-card-text class="about-paragraphs">
          If the added library does not include initialization from a single
          value, you will need to define it in the file. For example, filib does
          not include a single value initialization since its interval type is a
          struct, we define it as follow:
          <br />
          <br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">#define fic_interval(v) \
    {                   \
        v, v            \
    }</code></pre>
        </v-card-text>
        <v-card-title class="about-subtitle"
          >Defining functions for getting upper and lower bound</v-card-title
        >
        <v-card-text class="about-paragraphs">
          Since different libraries have different ways to get the upper and
          lower bound, we define a universal way of getting those values:
          <br />
          <br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">double lower(fic_interval v)
{
    return v.INF;
}
double upper(fic_interval v)
{
    return v.SUP;
}</code></pre>
        </v-card-text>
        <v-card-title class="about-subtitle">Operator overloading</v-card-title>
        <v-card-text class="about-paragraphs"
          >If the library does not include operator overloading, you will also
          need to define that yourself. For a concrete example, refer to
          <a
            href="https://github.com/geometryprocessing/intervals/blob/BIAS/src/interval.hpp"
            target="_blank"
            >src/interval.hpp</a
          >
          in
          <a
            href="https://github.com/geometryprocessing/intervals/tree/BIAS"
            target="_blank"
            >BIAS</a
          >
          branch.</v-card-text
        >
      </v-card>
    </div>

    <div class="center-intro">
      <v-card width="100%" color="rgba(20, 32, 36, 0.2)" dark elevation="10">
        <v-card-title class="about-titles">Adding new expression</v-card-title>
        <v-card-text class="about-paragraphs">
          In this benchmark, we have a total of 132 expressions. 28 of them in
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/src/methods.hpp"
            target="_blank"
            >src/methods.hpp</a
          >
          and the rest in
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/src/FPBench_methods.hpp"
            target="_blank"
            >src/FPBench_methods.hpp</a
          >. Looking at those two files should give you a general idea of what
          are needed for adding an expression. We will go in detail what and why
          they are needed here. We will take the
          <b><i>x_by_xy</i></b> expression in src/FPBench_methods.hpp as an
          example.
        </v-card-text>
        <v-card-title class="about-subtitle">Expression</v-card-title>
        <v-card-text class="about-paragraphs"
          >The most important thing is to define the expression itself, how it
          is computed. For our example, it is defined as: <br />
          <br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">{{ expression }}</code></pre>
          <br />The template is needed so that different interval types can all
          use this expression. Since the number of inputs for each expression
          can be different, we use a vector for input.
        </v-card-text>

        <v-card-title class="about-subtitle">Print method</v-card-title>
        <v-card-text class="about-paragraphs"
          >For correctness check, we need to know how to transfer this
          expression into a human-readable string. This is done by defining a
          print method. For this expression, it is defined as:<br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">{{ print_method }} </code></pre>

          <br />This should print something like: <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="bash">((2342163972178063/1125899906842624)) / (((2342163972178063/1125899906842624)) + ((4383221097164843/1125899906842624)))</code></pre>
          <br />
          The numbers here are the rational format of the randomly generated
          input.
        </v-card-text>
        <v-card-title class="about-subtitle">Check input</v-card-title>
        <v-card-text class="about-paragraphs"
          >Some functions may need special input range. Since we extracted this
          expression from an existing benchmark, the range is predefined. Hence,
          we just need to setup a function to check if the input is valid.
          <br />For this specific expression, we have
          1<span>&#8804;</span>v[0]<span>&#8804;</span>4 and
          1<span>&#8804;</span>v[1]<span>&#8804;</span>4. Hence, the input check
          is defined as:<br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">{{ check_inputs }}</code></pre>
          <br />
          If the input range is not specified, and we know that the denominator
          cannot be 0, we can always write an additional check as:<br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">if ((v[0] + v[1]) == 0)
    return false;</code></pre>
        </v-card-text>

        <v-card-title class="about-subtitle"
          >Input range and number</v-card-title
        >
        <v-card-text class="about-paragraphs"
          >The input range is also needed for the random generator. For this
          expression, we have the range predifined, hence we put the range for
          each input as a vector of distribution:<br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">{{ distribution }} </code></pre>
          <br />
          The number of inputs is also needed, and can be easily defined. For
          this expression, we know the number of inputs is 2:
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">const int x_by_xy_variable_count = 2;</code></pre>
        </v-card-text>
      </v-card>
    </div>
    <div class="center-intro">
      <v-card width="100%" color="rgba(20, 32, 36, 0.2)" dark elevation="10">
        <v-card-title class="about-titles"
          >Adding library to tests</v-card-title
        >
        <v-card-text class="about-paragraphs">
          For each test, if you want to add library, the syntax is similar. We
          will go over them here:
        </v-card-text>
        <v-card-title class="about-subtitle">
          Adding to interval size test</v-card-title
        >
        <v-card-text class="about-paragraphs">
          In
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/tests/gap/gap.cpp"
            target="_blank"
            >tests/gap/gap.cpp</a
          >, you will need to add one line to the macro function RUN_GAP to
          include your library. For example, if we want to use filib, the line
          is: <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">COMPUTE_GAP(fic_interval, "FILIB C", METHOD, INFO, VARIABLE_COUNT);</code></pre>
          <br />
          The first input is the interval type, the second is the interval name.
          The rest three inputs are from the expression that we run. You only
          need to support the first two arguments, the rest are inherited from
          the macro function.
        </v-card-text>

        <v-card-title class="about-subtitle">Adding to query test</v-card-title>
        <v-card-text class="about-paragraphs"
          >In
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/tests/query/query.cpp"
            target="_blank"
            >tests/query/query.cpp</a
          >, add one line to macro function RUN_QUERY:<br /><br />

          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">PRINT_QUERIES(fic_interval, "FILIB C", METHOD, PRINT_METHOD, VARIABLE_COUNT);</code></pre>
          <br />
          Again, you only need to support the first two argument and do not
          modify the rest.</v-card-text
        >

        <v-card-title class="about-subtitle">Adding to speed test</v-card-title>
        <v-card-text class="about-paragraphs"
          >In
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/tests/speed/speed.cpp"
            target="_blank"
            >tests/speed/speed.cpp</a
          >, you will need to define multiple things, the first is a timer,
          which is a double. This should be a global variable define at the top
          of the file: <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">double fic_timer;</code></pre>
          <br />
          Then, at each call of RUN_SPEED, the timer should be reset to 0:
          <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">fic_timer = 0;</code></pre>
          <br />
          Then, add the line to compute the time:
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">COMPUTE_TIME(fic_interval, METHOD, VARIABLE_COUNT, fic_timer);</code></pre>
          <br />
          Since we want to know the result at the end, we also need to print out
          the time:<br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">printf("TIME, %s, FILIB C, %lf us, %lfx time that FILIB C version took\n", INFO, fic_timer, fic_timer / fic_timer);</code></pre>
          <br />
          The print format is not limited to the one we provide above, However,
          this format is needed for our plotting script to work
          correctly.</v-card-text
        >
      </v-card>
    </div>
    <div class="center-intro">
      <v-card width="100%" color="rgba(20, 32, 36, 0.2)" dark elevation="10">
        <v-card-title class="about-titles">
          Add expression to tests</v-card-title
        >
        <v-card-text class="about-paragraphs"
          >Adding expressions to test is almost the same procedure. We will go
          over the three tests we have to demonstrate:
        </v-card-text>
        <v-card-title class="about-subtitle"
          >Adding to interval size test</v-card-title
        >
        <v-card-text class="about-paragraphs">
          In
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/tests/gap/gap.cpp"
            target="_blank"
            >tests/gap/gap.cpp</a
          >, you will need to add the lines that call this expression. We will
          use x_by_xy as an example. <br /><br />Since we may have pre-defined
          values, the first thing is to check if we want to use them by checking
          if the flag <b>USE_SYSTEM_RANDOM</b> exists:<br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">#ifndef USE_SYSTEM_RANDOM
    read_rationals_from_file("../rational_datas/x_by_xy_used_rationals.txt");
#endif</code></pre>
          <br />
          Then, to use the expression: <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">RUN_GAP(x_by_xy, "x_by_xy", x_by_xy_variable_count, x_by_xy_range, check_input_x_by_xy);</code></pre>
          <br />
          The first argument is the expression itself, the second is the name of
          the expression, then the number of variables, the range of inputs, and
          the function name that checks if the inputs are valid. Those things
          are defined when an expression is defined.
        </v-card-text>

        <v-card-title class="about-subtitle"
          >Adding to query test
        </v-card-title>
        <v-card-text class="about-paragraphs"
          >In
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/tests/query/query.cpp"
            target="_blank"
            >tests/query/query.cpp</a
          >, you will need to add similar lines to use the expression.
          <br /><br />The lines that checks if pre-defined values exists are the
          same. To use the expression: <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">RUN_QUERY(x_by_xy, print_x_by_xy, x_by_xy_variable_count, x_by_xy_range, check_input_x_by_xy);</code></pre>
          <br />
          Only the second input changed, which is now the method that prints the
          query, which we have explained before.
        </v-card-text>
        <v-card-title class="about-subtitle">
          Adding to speed test</v-card-title
        >
        <v-card-text class="about-paragraphs">
          For speed test, we do not need to check if pre-defined values exists
          since the number of inputs is significantly less. However, you can
          also use the pre-defined values if you want, the same code works fine.
          You need to modify
          <a
            href="https://github.com/geometryprocessing/intervals/blob/master/tests/speed/speed.cpp"
            target="_blank"
            >tests/speed/speed.cpp</a
          >
          for this one.<br /><br />
          To use the expression: <br /><br />
          <pre
            class="code-text"
            v-highlightjs
          ><code class="cpp">RUN_SPEED(x_by_xy, "x_by_xy", x_by_xy_variable_count, x_by_xy_range, check_input_x_by_xy);</code></pre>
          <br />
          The inputs are the same as adding to interval size test.
        </v-card-text>
      </v-card>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "About",
  components: {},
  data() {
    return {
      expression:
        "template <class T>\ninline T x_by_xy(const std::vector<T> &value_array)\n{\n\treturn value_array[0] / (value_array[0] + value_array[1]);\n}",
      print_method:
        'std::string print_x_by_xy(const std::vector<gmp::Rational> &v)\n{\n\treturn std::string() + "" + "(" + rational_to_string(v[0]) + ")" + " / (" + "(" + rational_to_string(v[0]) + ")" + " + " + "(" + rational_to_string(v[1]) + ")" + ")";\n}',
      check_inputs:
        "bool check_input_x_by_xy(const std::vector<double> &v)n{\n\tif (1.0 > v[0])\n\t\treturn false;\n\tif (v[0] > 4.0)\n\t\treturn false;\n\tif (1.0 > v[1])\n\t\treturn false;\n\tif (v[1] > 4.0)\n\t\treturn false;\n\treturn true;\n}",
      distribution:
        "const std::vector<std::uniform_real_distribution<double>> x_by_xy_range = {std::uniform_real_distribution<double>(1.0, 4.0), std::uniform_real_distribution<double>(1.0, 4.0)};",
    };
  },
};
</script>

